
.city-input {
  margin: 20vh 50%;
  translate: -50%;
  background: #000;
  width: 500px;
  height: 500px;
  border-radius: 50px;
  flex-direction: column;
  position: absolute;

  &-header {
    margin: 25px auto 20px auto;
    font-size: 40px;
    line-height: 47px;
    color: #fff;
    text-align: center;
  }

  &-form {
    margin: 20px auto 25px auto;
    display: flex;
    flex-direction: column;
  }

  &-input {
    margin: 0 auto;
    width: 350px;
    height: 35px;
    border: 5px solid #fff;
    background: transparent;
    text-align: center;
    color: #fff;
    line-height: 28px;
    padding: 10px;
    border-radius: 10px;
    font-size: 18px;
  }

  &-button {
    margin: 20px auto 25px auto;
    width: 200px;
    height: 40px;
    text-align: center;
    color: #000;
    font-size: 16px;
    line-height: 19px;
    padding: 3px 20px;
    border: 3px solid #fff;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;

    &:hover {
      background-color: #000;
      color: #fff;
      cursor: pointer;
    }

    &:disabled {
      background: transparent;
      color: #fff;
      cursor: not-allowed;
    }
  }

  p {
    margin: 0 auto 50px auto;
    font-size: 24px;
    line-height: 28px;
    color: #fff;
    text-align: center;
    span {
      text-decoration: underline;
    }
  }
}