.brief-sun {
  display: flex;
  flex-direction: row;
  align-items: center;
  grid-column: 1/5;
  grid-row: 3/3;
  p {
    text-align: center;
    font-size: 16px;
    line-height: 19px;
    color: #fff;
  }
  .sun-rise {
    margin: 0 10px 0 0;
  }
  svg {
    margin: 0 12px 0 12px;
  }
}