.weather-info {
  display: flex;
  flex-direction: column;
  position: absolute;
  margin: 15vh 50%;
  translate: -50%;
  width: 1000px;
  height: 600px;
  background: #000;
  border-radius: 50px;

  .loading {
    margin: 25vh 50%;
    translate: -50%;
    position: absolute;
    background-color: #fff;
    width: 250px;
    height: 50px;
    text-align: center;
    border: 3px solid #000;
    border-radius: 10px;

    &-text {
      color: #000;
      font-size: 16px;
      line-height: 19px;
    }
  }
  .icons {
    justify-content: space-evenly;
    margin: 0 120px 0 120px;
  }
}
